import React from "react";
import { Link } from "react-router-dom";
import { Dropdown, Menu } from "semantic-ui-react";

export default function MasterMenu() {
  return (
    <Menu.Item>
      <Dropdown pointing="top right" text="Master Maintenance">
        <Dropdown.Menu>
          <Dropdown.Item
            as={Link}
            to="/members"
            text="社員/パートナー"
            icon="users"
          />
          <Dropdown.Item
            as={Link}
            to="/clients"
            text="取引先"
            icon="building outline"
          />
          <Dropdown.Item as={Link} to="/projects" text="案件" icon="inbox" />
        </Dropdown.Menu>
      </Dropdown>
    </Menu.Item>
  );
}
