import React from "react";
import { Route } from "react-router-dom";
import { useSelector } from "react-redux";
import { ToastContainer } from "react-toastify";
import { Container } from "semantic-ui-react";
import ModalManager from "../common/modals/ModalManager";
import AuthContainer from "../../features/auth/AuthContainer";
import NowLoading from "../common/utils/NowLoading";
import HomePage from "../../features/home/HomePage";
import NavBar from "../../features/nav/NavBar";
import Dashboard from "../../features/dashboard/Dashboard";
import ProfilePage from "../../features/profiles/profilePage/ProfilePage";
import DataGrid1 from "../../features/datagrid/kind1/DataGrid1";
import DataGrid2 from "../../features/datagrid/kind2/DataGrid2";
import AssignPage from "../../features/assign/AssignPage";
import ClientDashboard from "../../features/master/clients/dashboard/ClientDashboard";
import ClientDetailedPage from "../../features/master/clients/detailed/ClientDetailedPage";
import MemberDashboard from "../../features/master/members/dashboard/MemberDashboard";
import MemberDetailedPage from "../../features/master/members/detailed/MemberDetailedPage";
import ProjectDashboard from "../../features/master/projects/dashboard/ProjectDashboard";
import ProjectDetailedPage from "../../features/master/projects/detailed/ProjectDetailedPage";

function App() {
  const { initialized } = useSelector((state) => state.async);

  if (!initialized) {
    return <NowLoading content="Loading Application..." />;
  }

  return (
    <>
      <ModalManager />
      <ToastContainer position="bottom-right" hideProgressBar />
      <Route exact path="/" component={HomePage} />
      <Route
        path={"/(.+)"}
        render={() => (
          <AuthContainer>
            <NavBar />
            <Container className="main">
              <Route exact path="/dashboard" component={Dashboard} />
              <Route path="/assign/:month" component={AssignPage} />
              <Route exact path="/datagrid1" component={DataGrid1} />
              <Route exact path="/datagrid2" component={DataGrid2} />
              <Route exact path="/profile" component={ProfilePage} />
              <Route exact path="/clients" component={ClientDashboard} />
              <Route path="/clients/:id" component={ClientDetailedPage} />
              <Route exact path="/members" component={MemberDashboard} />
              <Route path="/members/:id" component={MemberDetailedPage} />
              <Route exact path="/projects" component={ProjectDashboard} />
              <Route path="/projects/:id" component={ProjectDetailedPage} />
            </Container>
          </AuthContainer>
        )}
      />
    </>
  );
}

export default App;
