import React, { useEffect, useRef, useState } from "react";
import { Button, Input, Menu, Sidebar } from "semantic-ui-react";
import jexcel from "jexcel";
import "jexcel/dist/jexcel.css";
import "./style.css";
import columnsOptions from "./columnsOptions";
import data from "./assignData";

const options = {
  data: data,
  columns: columnsOptions,
  fullscreen: true,
};

export default function AssignPage() {
  const [visible, setVisible] = useState(false);

  const jexcelRef = useRef(null);
  useEffect(() => {
    jexcel(jexcelRef.current, options);
  }, []);

  return (
    <>
      <Sidebar.Pushable
        style={{
          position: "absolute",
          left: "0",
          right: "0",
          bottom: "0",
          top: "5em",
          overflow: "hidden",
        }}>
        <Sidebar
          as={Menu}
          animation="push"
          visible={visible}
          onHide={() => setVisible(false)}
          vertical
          direction="left">
          <Menu.Item>
            <Input type="text" icon="filter" />
          </Menu.Item>
        </Sidebar>
        <Sidebar.Pusher>
          <Button
            toggle
            active={visible}
            onClick={() => setVisible(!visible)}
            color="green"
            style={{ position: "absolute", top: "0.5em", left: "1em" }}>
            Filter
          </Button>
          <div ref={jexcelRef} style={{ marginTop: "4em" }} />
        </Sidebar.Pusher>
      </Sidebar.Pushable>
    </>
  );
}
